.admin-info-container {
    // opacity: 0.4;
    background: #3a3c43;
    width: 100%;
    height: 427px;
    display: inline-block;
    max-width: 1200px;
    overflow: visible;
    position: relative;
    text-align: left;
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin: 0 auto;
    color: #fff;

    &:before {
        content: "";
        border: 23px solid transparent;
        border-top-color: 3a3c43;
        position: absolute;
        bottom: 0px;
        transform: translateY(100%);
        left: 10.2%;
    }
}